.contacts {
  background: linear-gradient(to bottom, #ffffff 5%, #fff5f6 6% 100%);
  padding-bottom: 10px;

  @media screen and (min-width: 768px) {
    background: linear-gradient(to bottom, #ffffff 11%, #fff5f6 6% 100%);
  }

  @media screen and (min-width: 1200px) {
    background: url(../images/sectionbg.png),
      linear-gradient(to bottom, #ffffff 0%, #fff5f6 0 100%);
    background-repeat: no-repeat;
    background-position-y: center;
  }
}

.gallery__title {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.contacts__list {
  margin: 0;
  padding: 0;

  @media screen and (min-width: 768px) {
    display: flex;
  }
}
.contacts__item {
  padding: 43px 20px;
  background: $text-color-white;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  width: 220px;
  height: 427px;
  margin: 0 auto;
  list-style: none;

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  @media screen and (min-width: $tablet) {
    height: 544px;
    padding: 50px 31px;
    margin: 0;

    &:nth-child(-n + 3) {
      margin-bottom: 0;
    }

    &:nth-child(2) {
      margin-left: 20px;
      margin-right: 20px;
    }
  }

  @media screen and (min-width: 1200px) {
    width: 330px;
    height: 572px;
    margin: 0;

    &:nth-child(2) {
      margin-left: 31px;
      margin-right: 31px;
    }
  }
}

.contacts-cafe {
  display: flex;
  width: 80px;
  height: 16px;
  background: $background-products-cofe;
  border-radius: 4px;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 22px;
  align-items: center;
  text-align: center;
  letter-spacing: 0.04em;
  color: $text-color-white;
  margin-bottom: 25px;

  @media screen and (min-width: 768px) {
    width: 73px;
    height: 28px;
    font-size: 16px;
    line-height: 30px;
    margin-bottom: 30px;
  }
}

.contacts-foodtrack {
  display: flex;
  width: 130px;
  height: 16px;
  background: $background-products-green;
  border-radius: 4px;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 22px;
  align-items: center;
  text-align: center;
  letter-spacing: 0.04em;
  color: $text-color-white;
  margin-bottom: 25px;

  @media screen and (min-width: 768px) {
    width: 138px;
    height: 28px;
    font-size: 16px;
    line-height: 30px;
    margin-bottom: 30px;
  }
}

.contacts__name {
  margin: 0;
  margin-left: auto;
  margin-right: auto;
}

.contacts__city {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 22px;
  letter-spacing: 0.04em;
  color: $text-black;
  margin-top: 0;
  margin-bottom: 10px;

  @media screen and (min-width: 768px) {
    margin-bottom: 14px;
    font-size: 14px;
    line-height: 26px;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 14px;
    font-size: 16px;
    line-height: 30px;
  }
}

.contacts__text {
  display: block;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #907e82;
  margin: 0;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 23px;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 14px;
    font-size: 16px;
    line-height: 29px;
  }

  &::after {
    display: block;
    content: '';
    border: 1px solid #e1e1e1;
    width: 179px;
    height: 0px;
    margin-top: 25px;
    margin-bottom: 25px;

    @media screen and (min-width: 768px) {
      margin-top: 30px;
      margin-bottom: 30px;
      width: 157px;
    }

    @media screen and (min-width: 1200px) {
      margin-top: 32px;
      margin-bottom: 32px;
      width: 234px;
    }
  }
}

.workdays {
  display: block;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 22px;
  color: $text-black;
  margin: 0;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 26px;
  }
}

.workhours {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 22px;
  color: $background-hero-rose;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 26px;
  }

  &:nth-child(4) {
    margin-bottom: 10px;

    @media screen and (min-width: 768px) {
      margin-bottom: 16px;
    }

    @media screen and (min-width: 1200px) {
      margin-bottom: 15px;
    }
  }
}

.contacts-nav__mail {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #907e82;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 26px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 29px;
  }
}

.contacts-nav__phone {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #907e82;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 26px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 29px;
  }

  &:before {
    display: block;
    content: '';
    border: 1px solid #e1e1e1;
    width: 179px;
    height: 0px;
    margin-top: 25px;
    margin-bottom: 25px;

    @media screen and (min-width: 768px) {
      margin-top: 30px;
      margin-bottom: 30px;
      width: 157px;
    }

    @media screen and (min-width: 1200px) {
      margin-top: 32px;
      margin-bottom: 32px;
      width: 234px;
    }
  }
}

.button-locations {
  width: 161px;
  height: 44px;
  background: #d41342;
  border-radius: 22px;
  border: transparent;
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
  display: flex;
  align-items: center;
  text-align: center;
  color: $text-color-white;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  cursor: pointer;
  padding: 0;
  padding-left: 17px;
  transition: all 250ms ease-in-out;

  &:focus,
  &:hover {
    box-shadow: 0 0 5px 0 $text-color-rose inset, 0 0 2px 5px $text-color-rose;
    outline: transparent;
  }

  @media screen and (min-width: $tablet) {
    width: 191px;
    margin-right: 14px;
    margin: 0;
    padding-left: 30px;
  }

  @media screen and (min-width: 1200px) {
    width: 198px;
  }
}
.vector-right-white {
  margin-left: 8px;
}

.vector-right-red {
  margin-left: 14px;
}

.button-franchise {
  display: block;
  width: 161px;
  height: 44px;
  background: $text-color-white;
  border-radius: 22px;
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
  align-items: center;
  text-align: center;
  color: $text-button-chery;
  margin-left: auto;
  margin-right: auto;
  border: transparent;
  cursor: pointer;
  padding: 0;
  transition: all 250ms ease-in-out;

  &:focus,
  &:hover {
    box-shadow: 0 0 5px 0 $text-color-rose inset, 0 0 2px 5px $text-color-rose;
    background-color: $background-hero-hover2;
    outline: transparent;
  }

  @media screen and (min-width: 768px) {
    width: 174px;
    margin-left: 15px;
    margin-right: 0;
  }

  @media screen and (min-width: 1200px) {
    width: 180px;
  }
}

.contacts-button {
  margin-top: 54px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    display: flex;
    margin-top: 81px;
    align-items: baseline;
    justify-content: center;
  }

  @media screen and (min-width: 1200px) {
    margin-top: 86px;
  }
}

